.body {
  display: grid;
  grid-template-rows: min-content 1fr min-content;
  align-content: start;
  min-height: 100%;
  margin: 0;
  padding: 0;
  font-size: 16px;
  font-family: "Blogger Sans", "Arial", sans-serif;
  font-weight: 400;
  line-height: 1.375;
  color: #ffffff;
}
@media (max-width: 767px) {
  .body {
    overflow-x: hidden;
  }
}
@media (min-width: 768px) {
  .body {
    font-size: 28px;
    line-height: 1.17857;
  }
}
a {
  text-decoration: none;
}
img {
  max-width: 100%;
  height: auto;
}
.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}
