.check-in {
  position: relative;
  background-color: @special-blue-dark-2;
}

.check-in::after {
  content: "";
  position: absolute;
  top: 99%;
  left: 0;
  width: 100%;
  height: 29px;
  background-color: inherit;
  border-radius: 0 0 27px 27px;

  @media (min-width: @tablet-width) {
    height: 54px;
    border-radius: 0 0 50px 50px;
  }

  @media (min-width: @desktop-width) {
    height: 66px;
    border-radius: 0 0 55px 55px;
  }
}

.check-in__block {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: center;
  padding-top: 104px;
  padding-bottom: 6px;

  @media (min-width: @tablet-width) {
    padding: 150px 76px 6px 76px;
  }

  @media (min-width: @desktop-width) {
    padding: 220px 133px 42px 133px;
  }
}

.check-in__title {
  .zero-intervals;
  margin-bottom: 19px;

  @media (min-width: @tablet-width) {
    margin-bottom: 62px;
  }

  @media (min-width: @desktop-width) {
    margin-bottom: 59px;
  }
}

.check-in__text,
.check-in__text-bottom {
  .zero-intervals;
  margin-bottom: 30px;
  font-weight: 300;
  text-align: center;

  @media (min-width: @tablet-width) {
    max-width: 618px;
    margin-bottom: 66px;
    line-height: 1.171428;
  }

  @media (min-width: @desktop-width) {
    margin-bottom: 60px;
  }
}

.check-in__link {
  color: @basic-yellow;
  text-decoration: underline;
  text-decoration-color: inherit;
  text-decoration-skip-ink: none;
}

.check-in__link:hover,
.check-in__link:focus,
.check-in__link:active {
  color: @special-orange;
}

.check-in__link:active {
  opacity: 0.3;
}

.check-in__form {
  position: relative;
  display: flex;
  flex-direction: row;
  justify-content: stretch;
  width: 100%;
  margin-bottom: 28px;

  @media (min-width: @tablet-width) {
    align-self: stretch;
    margin-bottom: 58px;
  }

  @media (min-width: @desktop-width) {
    margin-bottom: 70px;
  }
}

.check-in__input {
  width: 100%;
  height: 62px;
  padding: 20px 62px 22px 20px;
  font-size: 18px;
  line-height: 1;
  font-weight: 700;
  color: @special-grey-0-3;
  background-color: @basic-white;
  border: 4px solid @basic-white;
  border-radius: 10px;
  outline: 0;

  @media (min-width: @tablet-width) {
    height: 80px;
    padding: 22px 82px 22px 30px;
    font-size: 24px;
    border-radius: 17px;
  }

  @media (min-width: @desktop-width) {
    padding-right: 335px;
  }
}

.check-in__input:focus::placeholder {
  opacity: 0;
}

.check-in__input:hover {
  color: @special-grey;
}

.check-in__input:focus {
  color: @basic-white;
  background-color: @special-blue-dark-2;
}

.check-in__input--error {
  color: @error-red;
}

.check-in__button {
  position: absolute;
  top: 0;
  right: 0;
  justify-content: center;
  width: 60px;
  height: 62px;
  .zero-intervals;
  border: none;
  border-radius: 10px;

  @media (min-width: @tablet-width) {
    width: 80px;
    height: 80px;
    border-radius: 0 17px 17px 0;
  }

  @media (min-width: @desktop-width) {
    justify-content: space-between;
    width: 325px;
    padding: 0 36px 0 36px;
  }
}

.check-in__button-text {
  display: none;

  @media (min-width: @desktop-width) {
    display: block;
    font-size: 24px;
    line-height: 1;
  }
}

.check-in__text-bottom {
  margin-bottom: 19px;

  @media (min-width: @tablet-width) {
    margin-bottom: 39px;
  }

  @media (min-width: @desktop-width) {
    margin-left: -10px;
    margin-bottom: 41px;
  }
}

.check-in__ours-contacts {
  @media (min-width: @desktop-width) {
    display: flex;
    flex-direction: row;
    align-self: center;
    margin-left: 110px;
  }
}
